
 .circle {
    position: absolute;
    top:40vh;
    left: 40vh;
    background: rgb(61,207,193);
    height: 20vh;
    width: 200px;
    height: 200px;
    border-radius: 40% 60% 70% 30% / 40% 50% 60% 50%;
    overflow: hidden;
    animation: morph 3s linear infinite;
  }
  
  .circle img {
    margin-top: 10px;
    width: 100%;
  }
  
  
  @keyframes morph{
    0%, 100% {
    border-radius: 40% 60% 70% 30% / 40% 40% 60% 50%;
    }
    34% {
      border-radius: 70% 30% 50% 50% / 30% 30% 70% 70%;
    }
    67% {
      border-radius: 100% 60% 60% 100% / 100% 100% 60% 60%;
    }
  }

  @media (max-width: 900px) {
    .circle {
      top: 40vh;
      left: 62vw; /* Stack the sections vertically on smaller screens */
    }

  }