html{
    cursor: url(./assets/cursors/windows95-cursor.png),auto;
}
body::-webkit-scrollbar {
    width: 12px; /* Set the width of the scrollbar */
}

body::-webkit-scrollbar-thumb {
    background-color: #000; /* Set the color of the scrollbar thumb */
    width: 100%;
}

body::-webkit-scrollbar-track {
    background-color: #fff; /* Set the color of the scrollbar track */
    border: 1px solid #000; /* Set the border of the track */
}

body{
    --scroll: 0;
    overflow-x: hidden;
    cursor: url(./assets/cursors/windows95-cursor.png),auto;
}